<template>
	<div class="page4_3">
		<div class="nav">
			<ul class="fl">
				<li>
					<router-link active-class="active" class="item" :to="{path:'/inside/page4_3'}">
						初稿生成
					</router-link>
				</li>
			</ul>
			<!-- <div class="fr inter">
				<interBtn title="初稿生成" id="more4_3"/>
			</div> -->
		</div>
		<div class="content_page">
			<div class="page-top">
				<Place class="fl" :data="msg"/>
				<div class="fr inter">
					<interBtn title="初稿生成疑点" id="more4_3" ydname="审计报告自动生成"/>
				</div>
			</div>
			<div>
				<div class="box box-border-shadow">
					<div class="content">
						<div v-if="showLoading" class="on-loading">
							<div class="text">
								<i class="el-icon-loading"></i>拼命加载中....初稿正在生成......
							</div>
						</div>
						<div class="content-box" v-else>
							<h1 class="title">某某会计师事务所</h1>
							<div class="content-boxs" style="line-height: 32px">
								<h1 style="text-align: center;font-size: 32px">{{content[0].yaosuneirong}}</h1>
								<h2 style="text-align: center;font-size: 16px">{{content[1].yaosuneirong}}</h2>
								<h3>{{content[2].yaosu}}:</h3>
								<p>{{content[2].yaosuneirong}}</p>
								<div v-for="(item,index) in content" v-if="index>2">
									<h3>{{item.yaosu}}</h3>
									<p>{{item.yaosuneirong}}</p>
								</div>
							</div>
							<h3 style="font-size: 22px;line-height: 44px;">附件：</h3>
							<table cellpadding="20" cellspacing="0" style="border: 1px solid #ccc;width: 100%">
								<tr>
									<td>表头</td>
									<td>指标</td>
									<td>2017年</td>
									<td>2018年</td>
									<td>单位</td>
								</tr>
								<tr v-for="value in content_table">
									<td>{{value.biaoming}}</td>
									<td>{{value.name}}</td>
									<td>{{value.caiwu_db_1}}</td>
									<td>{{value.caiwu_db_2}}</td>
									<td>{{value.danwei}}</td>
								</tr>
							</table>
						</div>
						<a class="word-export fr" href="javascript:void(0)"><i class="am-icon-download"></i> 导出报告 </a>
					</div>
				</div>
			</div>
		</div>		
	</div>
</template>
<script type="text/javascript">
	import interBtn from './interactions'
	import Place from './page/place'
	import reqData from "../../reqData.js"
	import saveAs from '../../../static/FileSaver.js'
	import '../../../static/jquery.wordexport.js'
	export default{
		name:'page4_3',
		components:{
			interBtn,
			Place
		},
		data(){
			return{
				msg:["审计报告自动生成","初稿生成"],
				content:[],
				content_table:{},
				showLoading:true
			}
		},
		computed:{
			companyName(){
				return JSON.parse(this.$store.getters.getCompany).value;
			},
			getFormData(){
				let formData=sessionStorage.getItem("baogaoForm");  
				return JSON.parse(formData);
			},
			getBgys(){
				let baogaoyaosu=JSON.parse(sessionStorage.getItem("baogaoyaosu"));
				let arr=[];
				baogaoyaosu.forEach((v)=>{
					v=v.slice(0,v.indexOf(" "));
					arr.push(v);
				});
				console.log(arr);
				return JSON.stringify(arr);
			}
		},
		methods:{
			getContent(){
				if(!this.getFormData){
					alert("请完成报告准备阶段");
					this.$router.push({ name: 'page4_1'});
				}else{
					this.content="";
					let AuditDoubt=sessionStorage.getItem("AuditDoubt");
					let AuditDoubt_fagui=sessionStorage.getItem("AuditDoubt_fagui"); 
					let yidian=sessionStorage.getItem("WordCloud");
					 reqData
				        .req({
				          apiName: "first_arcitle",
				          data: {             
				            bg_titl: this.getFormData.title,
				            yidian:yidian,
				            bg_id:this.getFormData.num,
				            sj_name:this.getFormData.name,
				            name:this.getFormData.author,
				            ys_id:this.getBgys,
				            gs_name:this.companyName,
				            AuditDoubt:AuditDoubt,
				            AuditDoubt_fagui:AuditDoubt_fagui
				          }
				        })
				        .then(res => {
				          res = res.data;
				          if (res && res.code === "200") {
				          	this.showLoading=false;
				            let data = res.data;
				            let data_cw = res.data_cw;
				            let data_cw_new=[];
				            console.log(data);
				            this.content=data;
				            for(var i=0;i<data_cw.name.length;i++){
				            	data_cw_new.push({
				            		biaoming:data_cw.biaoming[i],
				            		caiwu_db_1:data_cw.caiwu_db_1[i],
				            		caiwu_db_2:data_cw.caiwu_db_2[i],
				            		danwei:data_cw.danwei[i],
				            		name:data_cw.name[i],
				            	})
				            }
				            console.log(data_cw_new);
				            this.content_table=data_cw_new;
				   //          let reg2=new RegExp(/ /,"g");
				   //  		// let reg3=new RegExp(/\(.\)/,"g");
							// // cont=cont.replace(reg3,"</p><p>");
				   //  		cont=cont.replace(reg2,"</p><p>");
				   //          this.content="<p>"+cont+"</p>";
				            sessionStorage.setItem("first_arcitle", data);     
				          }
				        })
				        .catch(err => {
				          console.log(err);
				       });
				}
				
			}
		},
		mounted(){
			this.getContent();
			$(function(){
				$("a.word-export").click(function(event) {
					$(".content-box").wordExport("审计报告");
					 // $("#page-content")["wordExport"]("aa");
				});
			});
		}
	}
</script>
<style type="text/css">
	.page4_3 .content-box{
		color: #000;
		font-family: "宋体";
	}
	.page4_3 .content-box .title{
		text-align: center;
		font-family: 隶书;
		letter-spacing: 3.0000pt;
	    font-weight: bold;
	    font-size: 24.0000pt;
	}
/*	.page4_3 .content-box .label1,
	.page4_3 .content-box .label2,
	.page4_3 .content-box .label3,
	.page4_3 .content-box .label13,
	.page4_3 .content-box .text13{
		display: none;
	}*/
	.page4_3 .content-box .label{
		text-align: justify;
		text-indent:2em;
		letter-spacing:0.5px;
		font-size: 12pt;
		margin:6pt 0;
	}
	.page4_3 .content-box .text{
		text-align: justify;
		text-indent:2em;
		letter-spacing:0.5px;
		font-size: 12pt;
	}
	.end{
		text-align: justify;
		font-size: 12pt;
		margin-top: 63px;
	}
	.page4_3 .content-box .text1{
		text-align: center;
		font-size: 22pt;
		font-weight: bold;
		text-indent:0em;
		letter-spacing:6px;
		margin-top: 22px;
	}
	.page4_3 .content-box .text2{
		text-align: center;
		font-size: 10.5pt;
		text-indent:0em;
		letter-spacing:0px;
		margin: 22px 0;
	}
	.page4_3 .content-box .text3{
		text-align: left;
		text-indent:0em;
		letter-spacing:0px;
		font-size: 12pt;
	}
	.page4_3{
		position: relative;
	}
	.page4_3 .box{
		min-height: 600px;
    	text-align: center; 
    	margin: 20px auto;
        width: 100%;
        padding: 30px 60px;
        box-sizing: border-box;
        text-align: justify;
        border-radius: 8px;
        background-color: #fff;
	}
	.page4_3 .content{
		position: relative;
	}
	.page4_3 .content .word-export{
		position: absolute;
		right: 0;
		top: 0;
	}
	.page4_3 .content p{
		line-height: 26px;
		font-size: 16px;
	}
	.page4_3 .content .content-box{
		padding: 20px 50px;
		box-sizing: border-box;
	}
	.page4_3 .content .content-box .title{
		text-align: center;
		font-size: 30px;
		line-height: 44px;
	}
	.page4_3 .content .content-box .author{
		text-align: center;
		margin-bottom: 20px;
	}
	.page4_3 .content-boxs h3{
		font-size: 20px;
		line-height: 40px;
	}
	.page4_3 .content-boxs p{
		text-indent: 40px;
	}
	.page4_3 .content-box table td{
		border-bottom: 1px solid #ccc;
		border-right: 1px solid #ccc;
		padding: 10px;
		box-sizing: border-box;

	}
	.page4_3 .on-loading{
		position: fixed;
		top: 0;
		left: 0;
		right: 0;
		bottom: 0;
		background-color: #000;
		opacity: 0.6;
		font-size: 30px;
	}
	.page4_3 .on-loading .text{
		color: #b80000;
		position: absolute;
		left: 50%;
		top: 50%;
		margin-left: -46px;
	}
	.page4_3 .search{
		position: relative;
		width: 30%;
		margin: 0 auto;
		margin-bottom: 20px;
	}
	.page4_3 .inner_btn{
		position: absolute;
		right: -70px;
		top: 0;
	}
	.inner_num{
		position: absolute;
		left: -120px;
		top: 0;
		width: 120px;
	}
</style>


